import "./styles.css";
import { React, useState } from "react";

export default function App() {
  const [secret_key, setKey] = useState("Sample");
  const [website, setWebsite] = useState("Sample");
  const [password, setPassword] = useState("Generated Password");

  const small = "abcdefghijklmnopqrstuvwxyz";
  const capital = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
  const symbols = "!@#$%^&*()";

  const handleSubmit = (event) => {
    event.preventDefault();
  };

  const copyToClipboard = () => {
    navigator.clipboard.writeText(password);
    /* Alert the copied text */
    alert("Copied the text: " + password);
  };

  const compute = (arr, word) => {
    for (
      let i = 0, ind = 0;
      i < word.length;
      i++, ind = (ind + 1) % arr.length
    ) {
      if (small.search(word[i]) !== -1) {
        arr[ind] += small.search(word[i]) + 1;
      } else if (capital.search(word[i]) !== -1) {
        arr[ind] -= capital.search(word[i]) + 1;
      } else if (symbols.search(word[i]) !== -1) {
        arr[ind] += symbols.search(word[i]) + 1;
      } else {
        arr[ind] += parseInt(word[i], 10);
      }
    }
    return arr;
  };

  const hashFunction = () => {
    let arr = [0, 0, 0, 0, 0, 0, 0, 0];
    arr = compute(arr, secret_key);
    arr = compute(arr, website);

    for (let i = 0; i < arr.length; i++) {
      arr[i] = (arr[i] + 26) % 26;
    }
    let pass = "";
    pass += capital[arr[0]];
    pass += small[arr[1]];
    pass += small[arr[2]];
    pass += small[arr[3]];
    pass += symbols[arr[4] % 10];
    pass += (arr[5] % 10).toString();
    pass += (arr[6] % 10).toString();
    pass += (arr[7] % 10).toString();
    setPassword(pass);
  };

  return (
    <div className="App">
      <h1 className="Pad">
        <b>S</b>ecure<b>M</b>e
      </h1>
      <img src="../kerberos.jpeg" alt="Problems" height="300" width="250"></img>
      <center>
        <form onSubmit={handleSubmit}>
          <table className="Pad" cellPadding="10">
            <tbody>
              <tr>
                <td> Secret Key </td>
                <td>
                  {" "}
                  <input
                    type="text"
                    required
                    onChange={(e) => setKey(e.target.value)}
                  ></input>{" "}
                </td>
              </tr>
              <tr>
                <td> Website Name </td>
                <td>
                  {" "}
                  <input
                    type="text"
                    required
                    onChange={(e) => setWebsite(e.target.value)}
                  ></input>{" "}
                </td>
              </tr>
              <tr className="Middle">
                <td colSpan="2">
                  <span className="dotted" id="code">
                    {" "}
                    {password}{" "}
                  </span>
                  <button className="btn" onClick={copyToClipboard}>
                    <i className="fa fa-copy"></i>
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
          <button className="btn btn-success" onClick={hashFunction}>
            Generate
          </button>
        </form>
      </center>
    </div>
  );
}
